<template>
  <div class="mapbox-wrapper">
    <MglMap
      :access-token="accessToken"
      :map-style.sync="options.style"
      :center="options.center"
      :zoom="options.zoom"
      :pitch="options.pitch"
      :bearing="options.bearing"
      :container="options.container"
      :attribution-control="false"
      @load="onLoad"
    >
      <MglNavigationControl position="bottom-right" />
    </MglMap>
  </div>
</template>

<script>
import Mapbox from 'mapbox-gl'
import {
  MglMap,
  MglAttributionControl,
  MglNavigationControl,
  MglGeolocateControl,
  MglFullscreenControl,
  MglScaleControl 
} from 'vue-mapbox'

export default {
  components: {
    MglMap,
    MglNavigationControl,
  },
  data() {
    return {
      accessToken: process.env.VUE_APP_ACCESS_TOKEN, // your access token. Needed if you using Mapbox maps
      options: {
        style: 'mapbox://styles/mapbox/light-v10',
        center: [55.2752798, 25.1919927],
        zoom: 15.5,
        pitch: 65,
        bearing: -17.6,
        container: 'map',
        antialias: true,
      }, // your map style
    }
  },

  created() {
    // We need to set mapbox-gl library here in order to use it in template
    this.mapbox = Mapbox
    console.log(this.mapbox)
  },
  methods: {
    onLoad({ map, component }) {
      // Insert the layer beneath any symbol layer.
      const { layers } = map.getStyle()
      const labelLayerId = layers.find(
        layer => layer.type === 'symbol' && layer.layout['text-field'],
      ).id

      // The 'building' layer in the Mapbox Streets
      // vector tileset contains building height data
      // from OpenStreetMap.
      map.addLayer(
        {
          id: 'add-3d-buildings',
          source: 'composite',
          'source-layer': 'building',
          filter: ['==', 'extrude', 'true'],
          type: 'fill-extrusion',
          minzoom: 14,
          paint: {
            'fill-extrusion-color': '#aaa',
            // Use an 'interpolate' expression to
            // add a smooth transition effect to
            // the buildings as the user zooms in.
            'fill-extrusion-height': [
              'interpolate',
              ['linear'],
              ['zoom'],
              14,
              0,
              15.05,
              ['get', 'height'],
            ],
            'fill-extrusion-base': [
              'interpolate',
              ['linear'],
              ['zoom'],
              14,
              0,
              15.05,
              ['get', 'min_height'],
            ],
            'fill-extrusion-opacity': 0.6,
          },
        },
        labelLayerId,
      )

      // Add full screen control
      map.addControl(new this.mapbox.FullscreenControl())

      // Map resize
      map.resize()
    },
  },
}
</script>

